.main{
    width: 100%;
    height: 715px;
    background-image: url('../../assets/imgs/logo-bc-image.svg');
    background-repeat: no-repeat;
    background-position: 100% -368px;
    position: relative;
}

.title{
    width: 600px;
    font-size: 100px;
    font-weight: 600;
    color: #E3E7F2;
    position: absolute;
    right: 50px;
    top: 80px;
}


.btn{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 250px;
    height: 250px;
}

.btnWrapper{
    color: #E3E7F2;
    font-size: 30px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.btnIcon{
    align-self: end;
}