.wrapper{
    width: 100%;
    position: relative;
}

.listItem{
    width: 100%;
    height: 192px;
    color: #E3E7F2;
    font-weight: 600;
    font-size: 40px;
    border-bottom: 1px solid #E3E7F2;
    padding: 50px 30px;
    margin-bottom: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}

.listItemText{
    width: 40%;
}

.imgWrapper{
    height: 100%;
    position: absolute;
    top: -80px;
    right: 13%;
}

.img{
    position: sticky;
    top: 30%;
}

.num{
    font-weight: 900;
    color: #E3E7F2;
    opacity: 0.3;
}

.listItem:hover{
    background-color: #E3E7F2;
    transition: background-color 1s linear;
    color: black;
}